<template>
    <div class="booking-form">
        <Form class="form" @submit="makeBooking">
            <div class="row container-form">
                <div class="col-md-6 col-lg-4">
                    <label class="form-label">Puerto de carga</label>
                    <Field
                    readonly
                    v-model="form.port_loading"
                    type="text"
                    name="pol"
                    autocomplete="off"
                    class="form-control form-control-website"
                    ></Field>
                </div>
                <div class="col-md-6 col-lg-4">
                    <label class="form-label">Puerto de descarga</label>
                    <Field
                    readonly
                    v-model="form.port_discharge"
                    type="text"
                    name="pod"
                    autocomplete="off"
                    class="form-control form-control-website"
                    ></Field>
                </div>
                <div class="col-md-6 col-lg-4">
                    <label class="form-label">Disponibilidad carga</label>
                    <el-date-picker
                    v-model="form.ready_date"
                    class="date-picker"
                    format="DD-MM-YYYY"
                    />
                </div>
                <div class="col-md-6 col-lg-4 mt-3">
                    <label class="form-label">Barco</label>
                    <Field
                    readonly
                    v-model="form.vessel"
                    type="text"
                    name="vessel"
                    autocomplete="off"
                    class="form-control form-control-website"
                    ></Field>
                </div>
                <div class="col-md-6 col-lg-4 mt-3">
                    <label class="form-label">Viaje</label>
                    <Field
                    readonly
                    v-model="form.voyage"
                    type="text"
                    name="trip"
                    autocomplete="off"
                    class="form-control form-control-website"
                    ></Field>
                </div>
                <div class="col-md-6 col-lg-4 mt-3" v-if="user.customer_number !== 'FAK' && Object.keys(user).length">
                    <label class="form-label">Referencia del cliente</label>
                    <Field
                    readonly
                    v-model="form.customer_reference"
                    type="text"
                    name="reference"
                    autocomplete="off"
                    class="form-control form-control-website"
                    ></Field>
                </div>
                <div class="col-md-6 col-lg-4 mt-3">
                    <label class="form-label">Incoterms</label>
                    <IncotermsSelect
                    :incoterms_id="form.incoterms"
                    @update:incoterms_id="form.incoterms = $event"
                    />
                </div>
                <div class="col-md-6 col-lg-4 mt-3">
                    <label class="form-label">Moneda</label>
                    <Field
                    readonly
                    v-model="form.currency"
                    type="text"
                    name="currency"
                    autocomplete="off"
                    class="form-control form-control-website"
                    ></Field>
                </div>
                <div class="col-md-6 col-lg-4 mt-3">
                    <label class="form-label">Pago</label>
                    <Field
                    readonly
                    v-model="form.payability"
                    type="text"
                    name="payment"
                    autocomplete="off"
                    class="form-control form-control-website"
                    ></Field>
                </div>
                <!-- <div class="col-md-6 col-lg-4 mt-3">
                    <label class="form-label">Tipo de actor</label>
                    <Field
                    v-model="form.adress_type"
                    type="text"
                    name="actor_type"
                    autocomplete="off"
                    class="form-control form-control-website"
                    ></Field>
                </div> -->
                <div class="col-md-6 col-lg-4 mt-3">
                    <label class="form-label">Nombre del cargador</label>
                    <Field
                    v-model="form.shipper_name"
                    type="text"
                    name="load_name"
                    autocomplete="off"
                    class="form-control form-control-website"
                    ></Field>
                </div>
                <div class="col-md-6 col-lg-4 mt-3">
                    <label class="form-label">Contacto del cargador</label>
                    <Field
                    v-model="form.shipper_contact_name"
                    type="text"
                    name="load_contact_person"
                    autocomplete="off"
                    class="form-control form-control-website"
                    ></Field>
                </div>
                <div class="col-md-6 col-lg-4 mt-3">
                    <label class="form-label">Telfono del cargador</label>
                    <Field
                    v-model="form.shipper_contact_telephone"
                    type="text"
                    name="load_contact_number"
                    autocomplete="off"
                    class="form-control form-control-website"
                    ></Field>
                </div>
                <div class="col-md-6 col-lg-4 mt-3">
                    <label class="form-label">Direcin</label>
                    <Field
                    v-model="form.additional_inf_1"
                    type="text"
                    name="aditional_info1"
                    autocomplete="off"
                    class="form-control form-control-website"
                    ></Field>
                </div>
                <div class="col-md-6 col-lg-4 mt-3">
                    <label class="form-label">Cod. Postal</label>
                    <Field
                    v-model="form.additional_inf_2"
                    type="text"
                    name="aditional_info2"
                    autocomplete="off"
                    class="form-control form-control-website"
                    ></Field>
                </div>
                <div class="col-md-6 col-lg-4 mt-3">
                    <label class="form-label">Ciudad/Pas</label>
                    <Field
                    v-model="form.additional_inf_3"
                    type="text"
                    name="aditional_info3"
                    autocomplete="off"
                    class="form-control form-control-website"
                    ></Field>
                </div>
                <div class="col-md-6 col-lg-4 mt-3">
                    <label class="form-label">Nmeros de Paquetes</label>
                    <Field
                    v-model="form.number_packages"
                    type="text"
                    name="packages_number"
                    autocomplete="off"
                    class="form-control form-control-website"
                    ></Field>
                </div>
                <div class="col-md-6 col-lg-4 mt-3">
                    <label class="form-label">Tipo de artculo</label>
                    <Field
                    readonly
                    v-model="form.container_syze_type"
                    type="text"
                    name="article_type"
                    autocomplete="off"
                    class="form-control form-control-website"
                    ></Field>
                </div>
                <div class="col-md-6 col-lg-4 mt-3">
                    <label class="form-label">Cdigo arancelario</label>
                    <TariffCodeSelect
                    :cccode_id="form.item_commodity"
                    @update:cccode_id="form.item_commodity = $event"
                    />
                </div>
                <div class="col-md-6 col-lg-4 mt-3">
                    <label class="form-label">Descripcin de la mercanca</label>
                    <Field
                    v-model="form.item_description_goods"
                    type="text"
                    name="description"
                    autocomplete="off"
                    class="form-control form-control-website"
                    ></Field>
                </div>
                <div class="col-md-6 col-lg-4 mt-3">
                    <label class="form-label">Peso bruto (kg)</label>
                    <Field
                    v-model="form.item_cargo_gross_weight"
                    type="text"
                    name="brute_weight"
                    autocomplete="off"
                    class="form-control form-control-website"
                    ></Field>
                </div>
                <div class="col-md-6 col-lg-4 mt-3">
                    <label class="form-label">Peso neto (kg)</label>
                    <Field
                    v-model="form.item_cargo_net_weight"
                    type="text"
                    name="net_weight"
                    autocomplete="off"
                    class="form-control form-control-website"
                    ></Field>
                </div>
                <div class="col-md-6 col-lg-4 mt-3">
                    <label class="form-label">Cotizacin</label>
                    <Field
                    readonly
                    v-model="form.special_quotation"
                    type="text"
                    name="quotation"
                    autocomplete="off"
                    class="form-control form-control-website"
                    ></Field>
                </div>
                <div class="col-12 d-flex justify-content-center">
                    <div class="row mt-5">
                    <div class="col-md-12">
                        <div class="text-center">
                        <button ref="submitButton" class="container-button">
                            <span v-if="!loading"> HACER BOOKING </span>
                            <span v-else>
                            Espere
                            <span
                                class="spinner-border spinner-border-sm align-middle ms-2"
                            ></span>
                            </span>
                        </button>
                        </div>
                    </div>
                    </div>
                </div>
            </div>
        </Form>
    </div>
</template>

<script>
import { defineComponent, ref, computed, onMounted } from "vue";
import { Form, Field } from "vee-validate";
import ApiService from "@/core/services/ApiService";
import IncotermsSelect from "@/components/website/selects/IncotermsSelect.vue";
import TariffCodeSelect from "@/components/website/selects/TariffCodeSelect.vue";
import { useStore } from "vuex";
import { ElNotification } from "element-plus";


export default defineComponent({
    name: "Booking",
    props: {
        voyage: {
            type: Object,
            default() {
                return {}
            },
        },
        direct: {
            type: Boolean,
            default: false,
        },
    },
    components: {
        Form,
        Field,
        IncotermsSelect,
        TariffCodeSelect,
    },

    setup(props) {
        const store = useStore();
        const loading = ref(false);
        const user = computed(() => store.getters.currentUser);
        const bookingQuotation = computed(() => store.getters.BookingQuotationt);
        const form = ref({
            port_loading: "",
            port_discharge: "",
            ready_date: "",
            vessel: "",
            voyage: "",
            customer_reference: "",
            incoterms: "",
            currency: "",
            payability: "Prepago",
            adress_type: "SH",
            shipper_name: "",
            shipper_contact_name: "",
            shipper_contact_telephone: "",
            additional_inf_1: "",
            additional_inf_2: "",
            additional_inf_3: "",
            number_packages: "",
            container_syze_type: "",
            item_commodity: "",
            item_description_goods: "",
            item_cargo_gross_weight: "",
            item_cargo_net_weight: "",
            special_quotation: "",
            service: "",
        });
        const submitButton = ref<HTMLElement | null>(null);

        const makeBooking = () => {
            if (form.value.ready_date === "" || form.value.incoterms === "" ||
                form.value.number_packages === "" || form.value.container_syze_type === "" ||
                form.value.item_commodity === "" || form.value.item_description_goods === "" ||
                form.value.item_cargo_gross_weight === "" || form.value.item_cargo_net_weight === "") {
                ElNotification({
                    title: "Error",
                    message: "Todo los campos en el formulario son requeridos",
                    type: "error",
                });
            } else {
                loading.value = true
                ApiService.post("/api/bookings", {
                    port_loading: props.direct ? props.voyage.POL : props.voyage.header.tmh_pol,
                    port_discharge: props.direct ? props.voyage.POD : props.voyage.header.tmh_pod,
                    ready_date: form.value.ready_date,
                    vessel: props.direct ? props.voyage.rhvessel : props.voyage.header.rhvessel,
                    voyage: props.direct ? props.voyage.rhreise : props.voyage.header.rhreise,
                    customer_reference: form.value.customer_reference,
                    incoterms: form.value.incoterms,
                    currency: form.value.currency,
                    payability: "P",
                    adress_type: form.value.adress_type,
                    shipper_name: form.value.shipper_name,
                    shipper_contact_name: form.value.shipper_contact_name,
                    shipper_contact_telephone: form.value.shipper_contact_telephone,
                    additional_inf_1: form.value.additional_inf_1,
                    additional_inf_2: form.value.additional_inf_2,
                    additional_inf_3: form.value.additional_inf_3,
                    number_packages: form.value.number_packages,
                    container_syze_type: form.value.container_syze_type,
                    item_commodity: form.value.item_commodity,
                    item_description_goods: form.value.item_description_goods,
                    item_cargo_gross_weight: form.value.item_cargo_gross_weight,
                    item_cargo_net_weight: form.value.item_cargo_net_weight,
                    special_quotation: form.value.special_quotation,
                    service: form.value.service,
                    is_direct: props.direct?1:0,
                })
                .then(({ data }) => {
                    loading.value = false
                    console.log('data', data);
                })
                .catch(() => {
                  loading.value = false
                });
            }
        };

        onMounted(() => {
            form.value.port_loading = `${props.direct ? props.voyage.POLNAME : props.voyage.header.POLNAME}(${props.direct ? props.voyage.POL : props.voyage.header.tmh_pol})`;
            form.value.port_discharge = `${props.direct ? props.voyage.PODNAME : props.voyage.header.PODNAME}(${props.direct ? props.voyage.POD : props.voyage.header.tmh_pod})`;
            form.value.vessel = `${props.direct ? props.voyage.VESSELNAME : props.voyage.header.VESSELNAME}`;
            form.value.voyage = props.direct ? props.voyage.rhreise : props.voyage.header.rhreise;
            form.value.currency = bookingQuotation.value.moneda;
            form.value.container_syze_type = bookingQuotation.value.tipo_contendeor_code;
            form.value.special_quotation = bookingQuotation.value.code;
            form.value.service = props.direct ? props.voyage.service_code : props.voyage.header.rhservice;
            form.value.ready_date = props.direct ? props.voyage.ETA_POL : props.voyage.header.rheta;
            if (Object.keys(user.value).length) {
                form.value.customer_reference = user.value.customer_number
            }
          /*var fecha1 = new Date(props.direct ? props.voyage.ETA_POL : props.voyage.header.rheta).getTime() ;
          var dia = 2*24*60*60*1000
          var res = fecha1 - dia;
          res = new Date(res).toString();
          form.value.ready_date = res;*/
        });

        return {
            loading,
            form,
            user,
            submitButton,
            makeBooking,
        };

    },
});
</script>